<template>
    <div class = "analysis">
        <el-input class="content" type="textarea" autosize v-model="textToAnalyze" placeholder="请输入需要分析的文本"></el-input>
    </div>  
    <Horizon></Horizon>
    <div class="analyze-btn" @click="analyze" type="submit">Analyze</div>
</template>
<script>
import Horizon from "@/components/Horizon.vue"
    export default{
        name:"String",
        components:{
            Horizon

        },
        data(){
            return {
                textToAnalyze:""
            }
        },
        methods:{
            analyze(){
                const outputMode = sessionStorage.getItem("outputMode")
                const keyWordMode = sessionStorage.getItem("keyWordMode")
                const keyWord = sessionStorage.getItem("keyWord")
                const afterKey = sessionStorage.getItem("afterKey")
                const beforeKey = sessionStorage.getItem("beforeKey")
                console.log(outputMode)
                console.log(keyWordMode)
                console.log(keyWord)
                console.log(beforeKey)
                console.log(afterKey)
                const formData = new FormData();
                var contents=[];
                contents = this.textToAnalyze.split("\n");
                sessionStorage.setItem("text",this.textToAnalyze)
                formData.append("outputMode",outputMode)
                formData.append("keyWordMode",keyWordMode)
                if(keyWordMode){
                    formData.append("keyWord",keyWord)
                    formData.append("beforeKey",beforeKey)
                    formData.append("afterKey",afterKey)
                }
                for(let key in contents){    
                   formData.append(key,contents[key]);
                   console.log(formData.get(key)) 
                }
                this.axios({
                    method:"post",
                    url:"https://jsonplaceholder.typicode.com/posts/",
                    headers:{
                        "Content-Type":"multipart/form-data"
                    },
                    data:formData
                }).then((response)=>{
                    console.log(response);
                })
                sessionStorage.clear()
            },
        }
    }
</script>
<style scoped>
    
    .analyze-btn {
        background-color: #42b983;
        color: #fff;
        border: none;
        padding: 10px 20px;
        border-radius: 5px;
        cursor: pointer;
        width: 5%;
        margin: 0 auto;
    }

    .analyze-btn:hover {
        background-color: #42b983;
    }
</style>